<template>
  <ec-container title="成品烟包装规格申请" icon="onetomany" color-code="001">
    <ec-container-body>
      <ec-panel>
        <ec-panel-toolbar>
          <ec-panel-item>
            <ec-query-panel defaultFold @query="getTableData" @reset="onReset">
              <ec-query-item label="申请单位">
                <org-picker
                  allow-clear
                  limitLevel="2"
                  :disabled="orgLevelEk !== '1'"
                  v-model="listQuery.applyOrgCd"
                />
              </ec-query-item>
              <ec-query-item label="烟叶形态">
                <ec-select
                  showSearch
                  allowClear
                  placeholder="请选择"
                  v-model="listQuery.leafFormCd"
                >
                  <a-select-option
                    v-for="type in leafFormList"
                    :key="type.leafFormCode"
                    :value="type.leafFormCode"
                  >
                    {{ type.leafFormName }}
                  </a-select-option>
                </ec-select>
              </ec-query-item>
              <ec-query-item label="包装类型">
                <ec-select showSearch allowClear placeholder="请选择" v-model="listQuery.mouldType">
                  <a-select-option
                    v-for="type in packagesTypes"
                    :key="type.value"
                    :value="type.label"
                  >
                    {{ type.label }}
                  </a-select-option>
                </ec-select>
              </ec-query-item>
              <template #inner>
                <ec-query-item label="状态">
                  <ec-select
                    showSearch
                    allowClear
                    placeholder="请选择"
                    v-model="listQuery.specState"
                  >
                    <a-select-option
                      v-for="item in statusList"
                      :key="item.value"
                      :value="item.value"
                      >{{ item.label }}</a-select-option
                    >
                  </ec-select>
                </ec-query-item>
              </template>
            </ec-query-panel>
          </ec-panel-item>
          <ec-divider />
        </ec-panel-toolbar>
        <ec-panel-body>
          <ec-v-layout>
            <ec-v-item>
              <ec-panel-toolbar>
                <ec-panel-item-left>
                  <a-button type="primary" @click="onAdd('add')">新增</a-button>
                  <a-button :loading="deleteLoading" @click="onDelete">删除</a-button>
                  <a-button :loading="appearLoading" @click="onAppear">上报</a-button>
                </ec-panel-item-left>
              </ec-panel-toolbar>
            </ec-v-item>
            <ec-v-item>
              <ec-table
                ref="table"
                @checkbox-all="onSelTableData"
                @checkbox-change="onSelTableData"
                @page-action="handlePagination"
                :pagination="listQuery"
                :data="tableData"
                :loading="loading"
              >
                <ec-table-col width="80" type="checkbox" align="center"></ec-table-col>
                <ec-table-col title="序号" width="80" type="seq" align="center"></ec-table-col>
                <ec-table-col title="申请单位" field="applyOrgName" align="center"></ec-table-col>
                <ec-table-col title="烟叶形态" field="leafFormName" align="center"></ec-table-col>
                <ec-table-col title="包装类型" field="packType" align="center"></ec-table-col>
                <ec-table-col title="规格(KG/件)" field="spec" align="center"></ec-table-col>
                <ec-table-col title="状态" field="specState" align="center">
                  <template #default="{ row }">
                    <div v-if="row.specState === '0'" style="color: rgba(0, 0, 0, 0.447)">
                      <span style="margin-right: 4px">●</span>
                      <span>未上报</span>
                    </div>
                    <div v-else-if="row.specState === '1'">
                      <span style="color: #409eff; margin-right: 4px">●</span>
                      <span>已上报</span>
                    </div>
                    <div v-else style="color: rgba(0, 0, 0, 0.447)">
                      <span style="margin-right: 4px">●</span>
                      <span>已退回</span>
                    </div>
                  </template>
                </ec-table-col>
                <ec-table-col title="操作" field="priceTagName" align="center">
                  <template #default="{ row }">
                    <a-button v-if="row.specState !== '1'" type="link" @click="onAdd('edit', row)"
                      >编辑</a-button
                    >
                    <!-- <a-button type="link" @click="onAdd('details', row)">详细</a-button> -->
                  </template>
                </ec-table-col>
              </ec-table>
            </ec-v-item>
          </ec-v-layout>
        </ec-panel-body>
      </ec-panel>
      <ec-drawer
        :title="curType === 'add' ? '新增' : curType === 'edit' ? '编辑' : '详情'"
        :is-confirm="true"
        size="small"
        :visible="visible"
        @close="onClose"
      >
        <ec-container height="auto" fixed="top bottom" color-code="007">
          <ec-container-body>
            <ec-panel v-if="curType !== 'details'" color-code="001">
              <ec-panel-item>
                <ec-form :model="formData" :rules="formRules" ref="formRef1">
                  <a-row type="flex" :gutter="12">
                    <a-col :span="12">
                      <a-form-model-item label="申请单位" prop="applyOrgCd">
                        <org-picker
                          v-if="visible"
                          @ok="orgChange"
                          allow-clear
                          v-model="formData.applyOrgCd"
                        />
                      </a-form-model-item>
                    </a-col>
                    <a-col :span="12">
                      <a-form-model-item label="烟叶形态" prop="leafFormCd">
                        <a-select allowClear v-model="formData.leafFormCd" placeholder="请选择">
                          <a-select-option
                            v-for="item in leafFormList"
                            :key="item.leafFormCode"
                            :value="item.leafFormCode"
                            >{{ item.leafFormName }}</a-select-option
                          >
                        </a-select>
                      </a-form-model-item>
                    </a-col>
                  </a-row>
                  <a-row type="flex" :gutter="12">
                    <a-col :span="12">
                      <a-form-model-item label="包装类型" prop="packType">
                        <a-select allowClear v-model="formData.packType" placeholder="请选择">
                          <a-select-option
                            v-for="item in packagesTypes"
                            :key="item.value"
                            :value="item.label"
                            >{{ item.label }}</a-select-option
                          >
                        </a-select>
                      </a-form-model-item>
                    </a-col>
                    <a-col :span="12">
                      <a-form-model-item label="规格(KG/件)" prop="spec">
                        <a-select allowClear v-model="formData.spec" placeholder="请选择">
                          <a-select-option v-for="item in specList" :key="item.enumKey">{{
                            item.name
                          }}</a-select-option>
                        </a-select>
                      </a-form-model-item>
                    </a-col>
                  </a-row>
                  <!-- <a-row type="flex" :gutter="12">
                    <a-col :span="12">
                      <a-form-model-item label="状态">
                        <a-select disabled v-model="formData.specState" placeholder="请选择">
                          <a-select-option
                            v-for="item in statusList"
                            :key="item.value"
                            :value="item.value"
                            >{{ item.label }}</a-select-option
                          >
                        </a-select>
                      </a-form-model-item>
                    </a-col>
                  </a-row> -->
                </ec-form>
              </ec-panel-item>
            </ec-panel>
            <ec-panel v-else class="productDrawer" color-code="001" title="基本信息">
              <ec-panel-item>
                <a-row type="flex" :gutter="12">
                  <a-col :span="12">
                    申请单位: <span>{{ formData.applyOrgName }}</span>
                  </a-col>
                  <a-col :span="12">
                    烟叶形态: <span>{{ formData.leafFormName }}</span>
                  </a-col>
                </a-row>
                <a-row type="flex" :gutter="12">
                  <a-col :span="12">
                    包装类型: <span>{{ formData.packType }}</span>
                  </a-col>
                  <a-col :span="12">
                    规格: <span>{{ !!formData.spec ? `${formData.spec}KG/件` : '' }}</span>
                  </a-col>
                </a-row>
                <!-- <a-row>
                  <a-col :span="12">
                    状态: <span>{{ formData.specState === '0' ? '未上报' : '已上报' }}</span>
                  </a-col>
                </a-row> -->
              </ec-panel-item>
            </ec-panel>
          </ec-container-body>
          <ec-container-footer fixed>
            <a-space>
              <a-button
                type="primary"
                v-if="curType !== 'details'"
                :loading="saveLoading"
                @click="onSave"
                >保存</a-button
              >
              <a-button @click="onClose">关闭</a-button>
            </a-space>
          </ec-container-footer>
        </ec-container>
      </ec-drawer>
    </ec-container-body>
  </ec-container>
</template>
<script>
import { OrgPicker } from '@euler-material/ecm-ui';

export default {
  name: 'ProductApply',
  components: {
    OrgPicker,
  },
  data() {
    return {
      appearLoading: false,
      leafFormList: [
        { label: '片烟', value: '片烟' },
        { label: '烟梗', value: '烟梗' },
        { label: '烟末', value: '烟末' },
        { label: '烟丝', value: '烟丝' },
        { label: '碎片', value: '碎片' },
        { label: '废弃烟叶', value: '废弃烟叶' },
      ],
      packagesTypes: [
        { label: '麻片', value: '1' },
        { label: '烟框', value: '2' },
        { label: '烟箱', value: '3' },
      ],
      statusList: [
        { label: '已上报', value: '1' },
        { label: '已退回', value: '2' },
        { label: '未上报', value: '0' },
      ],
      saveLoading: false,
      deleteLoading: false,
      usingLoading: false,
      mouldTypeList: [
        { name: '模板类型1', id: '1' },
        { name: '模板类型2', id: '2' },
      ],
      curType: 'add',
      formData: {},
      formRules: {
        applyOrgCd: [{ required: true, message: '请选择申请单位', trigger: 'change' }],
        leafFormCd: [{ required: true, message: '请选择烟叶形态', trigger: 'change' }],
        spec: [{ required: true, message: '请输入规格', trigger: 'blur' }],
        packType: [{ required: true, message: '请选择包装类型', trigger: 'change' }],
      },
      visible: false,
      tableData: [],
      loading: false,
      listQuery: {
        applyOrgCd: undefined,
        mouldName: undefined,
        mouldType: undefined,
        pageSize: 10,
        total: 0,
        pageNo: 1,
      },
      total: 0,
      selectData: [],
      leafTypeList: [],
      specList: [],
    };
  },
  computed: {
    bizCode() {
      return this.$store.getters.getAppConfig.bizCode;
    },
    orgLevelEk() {
      return this.$store.getters.getAppConfig.orgLevelEk;
    },
    _api() {
      return this.$apis.packingSpec.apply;
    },
  },
  methods: {
    init() {
      // 默认状态
      if (this.orgLevelEk !== '1') {
        this.listQuery.applyOrgCd = this.bizCode;
      }
    },
    async getLeafForm() {
      const res = await this._api.queryLeafForm({});
      this.leafFormList = res ? res.rows : [];
    },
    orgChange(data) {
      const { title } = data.selectOption;
      this.formData.applyOrgName = title;
      // if (data && data.selectOption.length) {
      //   const { title } = data.selectOption[0];
      //   this.formData.applyOrgName = title;
      // }
    },
    onReset() {
      const { total } = this.listQuery;
      this.listQuery = {
        mouldName: undefined,
        mouldType: undefined,
        applyOrgCd: undefined,
        packType: undefined,
        pageSize: 10,
        pageNo: 1,
        total,
      };
    },
    onAppear() {
      if (!this.selectData.length) {
        return this.$message.error('请先选择数据');
      }
      for (const item of this.selectData) {
        const { specState } = item;
        if (specState !== '0') {
          return this.$message.error('只有未上报的数据才能上报');
        }
      }
      this.$confirm({
        title: '是否执行该操作?',
        content: () => (
          <div style="color:red;">
            是否确认上报选中的数据，确认请点击'确认'按钮，取消点击'取消'按钮。
          </div>
        ),
        onOk: () => {
          this.appearLoading = true;
          const ids = [];
          for (const item of this.selectData) {
            const { packSpecId } = item;
            ids.push(packSpecId);
          }
          const params = {
            packSpecId: ids,
          };
          this._api
            .reportedPcTPackSpec(params)
            .then(() => {
              this.getTableData();
              this.$message.success('上报成功');
            })
            .finally(() => {
              this.selectData = [];
              this.appearLoading = false;
            });
        },
      });
    },
    onDelete() {
      if (!this.selectData.length) {
        return this.$message.error('请先选择数据');
      }
      for (const item of this.selectData) {
        const { specState } = item;
        if (specState === '1') {
          return this.$message.error('选择的数据存在已上报的申请,无法删除');
        }
      }
      this.$confirm({
        title: '是否执行该操作?',
        content: () => (
          <div style="color:red;">
            是否确认删除选中的数据，确认请点击'确认'按钮，取消点击'取消'按钮。
          </div>
        ),
        onOk: () => {
          this.deleteLoading = true;
          const ids = [];
          for (const item of this.selectData) {
            const { packSpecId } = item;
            ids.push(packSpecId);
          }
          const params = {
            packSpecId: ids,
          };
          this._api
            .deletePcTPackSpec(params)
            .then(() => {
              this.getTableData();
              this.$message.success('删除成功');
            })
            .finally(() => {
              this.selectData = [];
              this.deleteLoading = false;
            });
        },
      });
    },
    getModuleTypeName(id) {
      if (!id) return '';
      const item = this.mouldTypeList.filter((v) => v.id === id);
      return item.length ? item[0].name : '';
    },
    onSelTableData({ records }) {
      this.selectData = records;
    },
    onSave() {
      console.log(11);
      this.$refs.formRef1.validate((valid) => {
        if (valid) {
          this.saveLoading = true;
          // this.formData.applyOrgCd = this.formData.applyOrgCd.length
          //   ? this.formData.applyOrgCd.join('')
          //   : '11000001000000000012';
          const leafForm = this.leafFormList.filter(
            (item) => item.leafFormCode === this.formData.leafFormCd,
          );
          console.log(this.curType);
          this.formData.leafFormName = leafForm.length ? leafForm[0].leafFormName : '';
          const { applyOrgCd } = this.formData;
          if (Array.isArray(applyOrgCd) && applyOrgCd.length) {
            this.formData.applyOrgCd = applyOrgCd[0];
          }
          // this.formData.leafTypeName = '烤烟';
          // this.formData.leafTypeCd = 'j0066666';
          const method =
            this.curType === 'add' ? this._api.createPcTPackSpec : this._api.updatePcTPackSpec;
          method(this.formData)
            .then(() => {
              const msg = this.curType === 'add' ? '新增成功' : '编辑成功';
              this.$message.success(msg);
            })
            .finally(() => {
              this.saveLoading = false;
              this.$refs.formRef1.resetFields();
              this.getTableData();
              this.onClose();
              this.formData = {};
            });
        }
      });
    },
    onClose() {
      if (this.curType !== 'details') {
        this.$refs.formRef1.resetFields();
      }
      this.formData = {};
      this.visible = false;
    },
    onAdd(type, row) {
      this.curType = type;

      this.formData = {};
      this.formData.specState = '0';
      if (type !== 'add') {
        this.formData = { ...row };
        this.formData.applyOrgCd = [this.formData.applyOrgCd];
        // this.formData.noticeMouldInfoId = row.noticeMouldInfoId;
      }
      this.visible = true;
    },
    handlePagination(pageNO, pageSize) {
      console.log(pageNO);
      this.listQuery.pageNo = pageNO;
      this.listQuery.pageSize = pageSize;
      this.getTableData();
      // this.onQuery();
    },
    getTableData() {
      this.loading = true;
      const { ...params } = this.listQuery;
      params.applyOrgCd = this.bizCode;
      if (typeof params.applyOrgCd !== 'string' && params.applyOrgCd !== undefined) {
        params.applyOrgCd = params.applyOrgCd.join('');
      }
      this._api
        .pageQueryFinishedPcTPackSpec(params)
        .then((data) => {
          this.tableData = data?.rows;
          this.listQuery.total = data?.totalRows;
        })
        .finally(() => {
          this.loading = false;
        });
    },
    async getLeafTypes() {
      $ec.apis.bim.leafType.query({}).then((data) => {
        this.leafTypeList = data?.rows;
        console.log(data);
      });
    },
    async getEnumTypelist() {
      const params = {
        enumTypeCodes: ['PC_PRODUCT_SPEC'],
      };
      const { enumMap } = await this.$apis.enum.index.getEnumType(params);
      this.specList = enumMap?.PC_PRODUCT_SPEC || [];
    },
  },

  async created() {
    this.init();
    this.getLeafForm();
    this.getEnumTypelist();
  },
};
</script>
<style lang="less" scoped>
.productDrawer {
  /deep/ .ant-col {
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    margin: 15px 0;
    span {
      padding-left: 5px;
    }
  }
}
</style>
